@import '~antd/dist/antd.css';
.paper-layout{
    border:1px solid #fbd1d1;
    padding:2rem;
    background-color: white;
    margin: 1rem 0;
    box-shadow: 10px 10px 5px #d6d5d5;
}
.paper-img-layout{
    border:1px solid #fbd1d1;
    padding:2rem;
    background-color: white;
    margin: 1rem 0;
    box-shadow: 10px 10px 5px #d6d5d5;
}
.paper-header-words{
    color: grey;
    padding-left: 0.2rem;
}
.paper-title{
    width: 100%;
    margin: 0.5rem 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.paper-header{
    margin-bottom: 0.5rem;
}
.paper-content{
    margin: 0.5rem 0;
}
.paper-footer{
    margin: 0.5rem 0;
}
.paper-sider-img:hover{
    cursor: pointer;
    transform: scale(1.1);
}
.paper-sider-img{
    height: auto;
    width: 100%;
}
.paper-sider-words{
    min-height: 8rem;
    text-align: justify;
    font-size: 1.15rem;
    overflow: hidden;
}
.paper-title{
    color: #337ab7;
    font-size: 2rem;
}
.paper-footer-readmore{
    font-size: 1rem;
}

/*.col-sm-* 小屏幕 平板 (≥768px)*/
@media screen and (max-width: 992px){
    /*分页信息*/
    .ant-pagination-item {
        height: 2.8rem;
        line-height: 2.8rem;
        width: 2.8rem;
    }
    .ant-pagination-prev, .ant-pagination-next {
        height: 2.8rem;
        line-height: 2.8rem;
        width: 2.8rem;
    }
    .paper-detail-body  span{
        font-size: 1rem !important;
    }
}
/*.col-md-* 中等屏幕 桌面显示器 (≥992px)*/
/*.col-lg-* 大屏幕 大桌面显示器 (≥1200px)*/
@media screen and (min-width: 992px) {
    .ant-pagination-item {
        height: 3rem;
        line-height: 3rem;
        width: 3rem;
    }
    .ant-pagination-prev, .ant-pagination-next{
        height: 3rem;
        line-height: 3rem;
        width: 3rem;
    }
    .paper-detail-body  span{
        font-size: 1.5rem !important;
    }
}
/*文章详情*/
.paper-detail-body section{
    background-color: white;
    margin-top: 2rem;
}
.paper-detail-body img{
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
}
/*文章分类列表*/
.detail-type-list{
    font-size: 2rem;
    cursor: pointer;
}
/*关于*/
.about-list-words{
    font-size: 1.5rem;
}
.ant-collapse-header{
    background-color: white;
}
.ant-collapse-content-box{
    color: #888;
    background-color: #eae6f3;
    border-left: 0.7rem solid #beacf3;
    padding: .2rem .3rem;
    position: relative;
    text-indent: 2em;
    margin: 0;
}